import React from 'react'
import {Nav, Navbar, NavDropdown, Form, FormControl, Button} from 'react-bootstrap'
import { Link } from "gatsby"

export default function Navbar2() {
  return (
    <div className="mb-12 flex">
      <br />
      <Navbar bg="light" expand="lg" fixed="top">
        <Link to="/blog"><Navbar.Brand>NIA - CRPIP</Navbar.Brand></Link>
        <Navbar.Toggle aria-controls="basic-navbar-nav" />
          <Navbar.Collapse id="basic-navbar-nav">
            <Nav className="ml-auto">
            <Nav.Link href="/blog/category/activity">Activities</Nav.Link>
            <Nav.Link href="/blog/category/news">News</Nav.Link>
            <Nav.Link href="http://omcrs.nia.gov.ph/">Memorandum Circular</Nav.Link>
            <Nav.Link href="/about">About Us</Nav.Link>
            <Nav.Link href="/contacts">Contacts</Nav.Link>
            </Nav>
          </Navbar.Collapse>
      </Navbar> 
    </div>
  )
}
